          <th>Price(€)</th>
          <th>Total</th>
          <th></th>
        </tr>
      </thead>
      <tbody v-for="(ev, index) in this.events_added">
        <tr>
          <td>{{ ev.event.name }}</td>
          <td>
            <h5> {{ quantity_events_added[ev.event.name] }}
            <button type="button" class="btn btn-success" style="width:auto%" @click="addTicket(ev)">+</button>
            <button type="button" class="btn btn-danger" style="width:auto%" @click="substractTicket(ev)">-</button>
            </h5>
          </td>
          <td>{{ ev.event.price }}</td>
          <td>{{ ev.event.price * quantity_events_added[ev.event.name]}}</td>
          <td>
